export default {
  data () {
    return {
      loading: false,
      title: '',
      author: '',
      img: '',
      time: '',
      content: []
    }
  },
  mounted () {
    this.$store.commit('SET_RETURN_TEXT', '前沿技术')
    this.$store.commit('SET_RETURN_URL', '/tech')
    this.getDetail()
  },
  methods: {
    getDetail () {
      this.title = '日本提出向中国人收“二维码使用费”？内容不实日本提出向中国人收“二维码使用费”？内容不实'
      this.author = '是大润发'
      this.img = ''
      this.time = '2019-01-02'
    }
  }
}



